@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');

.Home {
    padding-top: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 200px;
}

.face-header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Name {
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
    font-size: 40px;
    border-style: none;
    margin-top: 20px;
}

.Face {
    height: 120px;
    border-radius: 50%;
    border-width: 4px;
    border-style: solid;
    border-color: mediumvioletred;
}

.Work-Section {
    padding: 60px 80px;
    display: flex;
    height: fit-content;
    margin-top: 150px;
    -webkit-box-shadow: 0px 8px 20px 5px rgba(0,0,0,0.15);
    -moz-box-shadow: 0px 8px 20px 5px rgba(0,0,0,0.15);
    box-shadow: 0px 8px 20px 5px rgba(0,0,0,0.15);
    border-radius: 10px;
    width: 1100px;
    height: 300px;
    transition:all 0.3s ease;
}

.Work-Section:hover {
    opacity: 0.5;
    transform: scale(1.1);
    cursor: pointer;
}

.Work-Text {
    /* color:green; */
    font-family: 'Avenir';
    display: flex;
    flex-direction: column;
    align-self: center;
    margin-right: 180px;
}

.Work-Title {
    font-weight: 600;
    font-size: 22px;
}

.Work-Body {
    font-size: 18px;
    margin-top: 20px;
    line-height: 22px;
}

.Work-Date {
    font-size: 18px;
    color: #16acac;
    margin-top: 20px;
}

.Insta {
    background-image: repeating-radial-gradient(circle farthest-corner at 0% 100%, #d77c3d, #c63f78 52%, #aa3f9e 80%, #5156bd);
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    border-radius: 20%;
}

.Instalogo {
    width: 240px;
    height: 240px;
    filter: grayscale(1) invert(1);
    padding: 20px 30px;
    align-self: center;
}

.fulcrum {
    display: flex;
    justify-content: center;
}

.fulcrumLogo {
    width: 300px;
    align-self: center;
}

.purdueLogo {
    width: 300px;
    align-self: center;
    border-radius: 20px;
}

@font-face {
    font-family: "Avenir";
    src: url("../../Resources/Fonts/Avenir Roman.otf") format("opentype");
}

@media (max-width: 812px) {
    .Home {
        padding-top: 120px;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 100px;
    }
    
    .face-header {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    .Name {
        font-family: 'Montserrat', sans-serif;
        font-weight: 900;
        font-size: 20px;
        border-style: none;
        margin-top: 20px;
    }
    
    .Face {
        height: 80px;
        border-radius: 50%;
        border-width: 4px;
        border-style: solid;
        border-color: mediumvioletred;
    }
    
    .Work-Section {
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        align-content: center;
        height: fit-content;
        margin-top: 100px;
        border-radius: 10px;
        width: 210px;
    }
    
    .Work-Text {
        font-family: 'Avenir';
        display: flex;
        flex-direction: column;
        /* margin-right: 180px; */
        margin-top: 40px;
        margin-left: 180px;
        width: 140%;
    }
    
    .Work-Title {
        font-weight: 600;
        font-size: 18px;
    }
    
    .Work-Body {
        font-size: 16px;
        margin-top: 20px;
        line-height: 18px;
    }
    
    .Work-Date {
        font-size: 18px;
        color: #16acac;
        margin-top: 20px;
    }
    
    .Insta {
        background-image: repeating-radial-gradient(circle farthest-corner at 0% 100%, #d77c3d, #c63f78 52%, #aa3f9e 80%, #5156bd);
        width: 240px;
        height: 240px;
        display: flex;
        justify-content: center;
        border-radius: 20%;
    }
    
    .Instalogo {
        width: 192px;
        height: 192px;
        filter: grayscale(1) invert(1);
        padding: 20px 30px;
        align-self: center;
    }
    
    .fulcrum {
        display: flex;
        justify-content: center;
    }
    
    .fulcrumLogo {
        width: 240px;
        align-self: center;
    }
    
    .purdueLogo {
        width: 240px;
        align-self: center;
        border-radius: 20px;
    }
}