    height: 300px;
    -webkit-transition:all 0.3s ease;
    transition:all 0.3s ease;
}

.Work-Section:hover {
    opacity: 0.5;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

